// .contacts {}

.contacts__inner {
  display: flex;
  justify-content: space-between;

  gap: 30px;

  padding-left: max(15px, calc((100% - 1280px) / 2));

  @include _992 {
    flex-wrap: wrap;
  }
}

.contacts__info {
  margin-top: 50px;
  flex: 0 1 30%;

  @include _768 {
    flex: 0 1 100%;
    margin-bottom: 20px;
  }
}

.contacts__title {
  font-family: $bitter;
  font-weight: 500;
  font-size: 38px;
  line-height: 1.4;
  color: #061428;
  margin-bottom: 64px;
}

.contacts__wrap {
  max-width: 245px;
  margin-bottom: 92px;
}

.contacts__wrap > * {
  font-family: $roboto;
  font-weight: 400;
  font-size: 18px;
  color: #061428;

  &:not(:last-child) {
    margin-bottom: 44px;
  }
}

.contacts__heading {
  font-family: $roboto;
  font-weight: 400;
  font-size: 18px;
  line-height: 1.4;
  color: rgba(57, 57, 57, 0.8);
  margin-bottom: 10px;
}

.contacts__phone {
  display: block;
}

.contacts__phone-number {
  transition: color 0.3s;
  &:focus {
    color: #CD8A26;
  }
  &:hover {
    color: $orange;
  }
  &:active {
    color: #CD8A26;
  }
}

.contacts__phone-icon {
  width: 21px;
  height: 21px;
  margin-right: 8px;
}

.contacts__address {
  padding-left: 34px;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/location2.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 19px;
    height: 25px;
  }
}

.contacts__time {
  padding-left: 34px;
  position: relative;
  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    background-image: url(../img/time2.svg);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    width: 21px;
    height: 21px;
  }
}

.contact__button {
  max-width: 275px;
}

.contacts__map {
  width: calc(100% - 490px - 30px);
  flex: 0 1 70%;

  @include _992 {
    flex: 0 1 100%;
  }
}

.contacts__map iframe {
  display: block;
  width: 100%;
  height: 803px;

  @include _768 {
    height: 300px;
  }
}